html,
body,
img,
fieldset,
abbr,
acronym {
	border: 0;
	max-width: 100%;
	width: auto;
}
 
body {
  font-size: 16px;
  color: #000;
  line-height: 32px; 
  margin: 0;
  background-color: #F4F7FC;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
}

html, body, .BBPRoot {
  height: 100%;
}

*, ::after, ::before {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}


/* Scroll Bar */

* {
  scrollbar-width: thin;
  scrollbar-color: #00000033 #00000018;
}

*::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  border-radius: 50px;
  -webkit-transition: all ease 0.2s;
  transition: all ease 0.2s;
  overflow: auto;
  margin: auto;
}

*::-webkit-scrollbar-track {
  border-radius: 50px;
  overflow: auto;
  overflow-y: scroll;
  overflow-x: scroll;
  background: #c4c5c8;
}

*::-webkit-scrollbar-thumb {
  background: #0000;
  border-radius: 50px;
}

*:hover::-webkit-scrollbar-thumb {
  background: #00000050;
  transition-duration: 10s;
}
 
.BBPApp {
  height: 100%;
  padding: 16px;
  padding-bottom: 0;
}
 
.BBPApp .BBPAInner {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: auto;
}

a.BBPTableBtn, 
button.BBPTableBtn {
  background-color: #fff;
  border-radius: 4px;
  color: #5A607F;
  font-family: Poppins;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.01em;
  text-transform: capitalize;
  box-shadow: unset;
  line-height: normal;
  padding: 6px 16px;
  display: flex;
  align-items: center;
  text-decoration: none;
}
a.BBPTableBtn span {
  font-size: 16px;
  display: inherit;
  margin-right: 8px;
  margin-left: -4px;
}
a.BBPTableBtn span svg,
button.BBPTableBtn span svg.MuiSvgIcon-root {
  font-size: 16px;
}

a.BBPTableBtn:hover,
button.BBPTableBtn:hover {
  background-color: #175783;
  color: #fff;
}

button.BBPButton {
  font-weight: normal;
  font-size: 14px;
  letter-spacing: 0em;
  line-height: normal;
  text-align: left;
  color: #fff;
  border-radius: 10px;
  background-color: #175783;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  padding: 10px 35px;
  text-decoration: none;
  border: 2px solid #175783;
}

button.BBPButton[disabled] {
  color: #fff;
  opacity: .5;
}

button.BBPButton:hover {
  background-color: #175783;
  border: 2px solid #175783;
}
 
button.BBPButton.BBPBRed {
  background-color: #ee2505;
  border: 2px solid #ee2505;
}
 
button.BBPButton.BBPOButton {
  background: unset;
  color: #175783;
}

.BBPApp.BBPLP {
  padding: 0;
}

.BBPApp.BBPLP .BBPBodyView {
  padding: 0;
}

button.BBPButton .MuiLoadingButton-loadingIndicator {
  color: #fff;
}

/* Forms */

